body {
  height: 100vh;
  margin-top: 7rem;
  font-family: sans-serif;
}

.optionBtn {
  height: 10rem;
  width: 12rem;
  margin: .4rem;
  box-shadow: 5px 5px 5px 1px #000;
}

.optionImg {
  width: 100%;
  height: 100%;
}

#paperBtn {
  width: 13rem;
}

.optionBtn:hover {
  scale: .985;
}

.headLineDiv {
  display: flex;
}

#main, .RulesAndHeadLineDiv {
  flex-flow: column wrap;
  align-items: center;
  display: flex;
}

.headLine {
  text-align: center;
}

ul {
  list-style: none;
}

.topList {
  justify-content: space-around;
  display: flex;
}

.usrNameItem {
  margin: .5rem;
  display: flex;
  position: relative;
  top: 50vh;
}

.displayDiv, .displayDiv div {
  justify-content: space-evenly;
  margin: 1rem;
  display: flex;
}

.displayDiv div {
  width: 15rem;
