  flex-flow: column wrap;
}

.displayDiv {
  text-align: center;
}

.buttonsForm {
  justify-content: center;
  display: flex;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

#getUsrNameForm {
  width: max-content;
  background: #151414;
  border: 1px solid #302d2d;
  border-radius: 4px;
  margin: 40px auto;
  padding: 25px 28px;
  font-family: Segoe UI, sans-serif;
  animation: .4s cubic-bezier(.68, -.55, .27, 1.55) popup;
  position: relative;
  top: 25%;
}

@keyframes popup {
  0% {
    opacity: 0;
    transform: scale(.2);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

h2 {
  text-align: center;
  color: #e7e7e7;
  margin-bottom: 20px;
  font-size: 28px;
  font-weight: 400;
}

.input-parent {
  margin-bottom: 20px;
  display: block;
}

label {
  color: #a4a4a4;
  margin-bottom: 8px;
  font-size: 16px;
  display: block;
}

.input-parent input {
  width: 100%;
  color: #c7c7c7;
  background: #323131;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 10px 8px;
  font-size: 16px;
  transition: all .2s;
}

.input-parent input:hover {
  background: #404040;
}

.input-parent input:focus {
  box-shadow: 0 0 0 1px #0087ff;
}

.usrNameSubmit {
  width: 100%;
  color: #f4f4f4;
  background: #1a3969;
  border: none;
  border-radius: 4px;
  padding: 10px 18px;
  font-size: 15px;
  transition: all .2s;
}

.usrNameSubmit:hover {
  opacity: .9;
}

.usrNameSubmit:focus {
  box-shadow: 0 0 0 3px #000;
}

header img {
  height: 80px;
  margin-left: 40px;
}

header {
  height: 80px;
  background-color: #fff;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 25px #000;
}

header * {
  display: inline;
}

header li {
  margin: 20px;
}

header li p {
  color: #000;
  text-decoration: none;
}

.styled-table {
  border-collapse: collapse;
  min-width: 400px;
  max-width: 30%;
  margin: 25px 0;
  font-family: sans-serif;
  font-size: .9em;
  position: relative;
  box-shadow: 0 0 20px #00000026;
}

#high-score-div {
  text-align: center;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#reset-scores {
  width: 25rem;
  color: #f4f4f4;
  background: #1a3969;
  border: none;
  border-radius: 4px;
  padding: 10px 18px;
  font-size: 15px;
  transition: all .2s;
}

.styled-table thead tr {
  color: #fff;
  background-color: #193560;
}

.styled-table th, .styled-table td {
  padding: 8px 15px;
}

.styled-table tbody tr {
  border-bottom: 1px solid #ddd;
}

.styled-table tbody tr:nth-of-type(2n) {
  background-color: #f3f3f3;
}

.styled-table tbody tr:last-of-type {
  border-bottom: 1.5px solid #193560;
}

